#projects-container {
  position: relative;
  width: calc(100vw - 2em);
  height: 530px;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: 10em;

  .section-header {
    position: relative;
    display: flex;
    width: auto;
    height: 45px;
    margin-top: 2em;
    margin-bottom: 0.3em;
    //   background-color: salmon;

    #projects-icon {
      position: relative;
      margin: 0.2rem;
      width: 20px;
      height: 100%;
      align-self: center;
      fill: white;
    }
    > h3 {
      margin-left: 0.5em;
      margin-top: 2px;
      align-self: center;
      color: white;
      font-size: 1.7em;
    }
  }

  .project-card-grid {
    position: relative;
    display: grid;
    width: 100%;
    height: 480px;
    opacity: 0.7;
    background-color: rgb(100, 100, 100);

    &:before {
      content: "";
      position: absolute;
      width: inherit;
      height: 100%;
      top: 0;
      left: 0;
      background-color: rgb(78, 78, 78);
      opacity: 0.5;
      z-index: -1;
    }

    .project-card {
      position: relative;
      width: 350px;
      height: 466px;
      display: grid;
      background: linear-gradient(180deg, #a7a7a7 -35.4%, #000000 100%);
      margin: 0.3em;
      border-radius: 8px;
      border: 1px solid white;
      border-style: inset;
      z-index: 10;
      grid-template-columns: 1;
      grid-template-rows: 14% 54% 32%;
      z-index: 10;

      .project-title {
        position: relative;
        // width: 100%;
        // height: (14%);

        background-color: black;

        border-radius: 8px 8px 0 0;
        z-index: -14;
        display: grid;
        justify-content: center;
        align-items: center;

        > p {
          color: white;
          font-size: 1.3rem;
          font-weight: 600;
        }
      }
      .project-image-container {
        position: relative;
        height: 100%;
        // height: 68%;
        // background-size: cover;
        overflow: hidden;

        #project-image {
          position: relative;
          height: calc(145%);
          transform-origin: 50% 50%;
          left: -5%;
          top: -20%;
          background-repeat: none;
          overflow: hidden;
        }
      }
      .project-details {
        position: relative;
        background: linear-gradient(180deg, #a7a7a7 -65%, #000000 100%);
        border: 1.5px solid #ffffff;

        > p {
          position: relative;
          color: white;
          margin: 1.2rem;
        }
      }
    }
  }
}
